@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Fredoka One', cursive;
  font-size: 32px;
  color: #800000;
  margin-top: 150px;
}

.local-time {
    border: 5px #800000 dotted;
    border-radius: 10px;
    padding: 10px;
    width: 150px;
    height: 180px;
    transition: transform .2s;
    margin-left: 50px;
    margin-top: 50px;
    float: left;
}
.local-time:hover {
    transform: scale(1.3);
}
.local-month-year {
    font-size: 14px;
    color: #ccc;
}
.local-weekday {
    font-family: 'Monoton', cursive;
    font-size: 38px;
    color: #ffa500;
}
.local-seconds {
    font-size: 18px;
}
.time-location {
    font-size: 24px;
}
.navBar {
    border-bottom: #ccc 2px solid;
    width: 100%;
    height: 150px;
    position: fixed;
    top: 0;
    background-color: #fff;
}
.clock-logo {
    float: left;
    width: 80px;
    margin: 28px 10px 0 20px;
}
.app-title {
    float: left;
    width: 400px;
}
.search-bar {
    float: right;
    margin: 60px 50px 0 0;
    width: 500px;
    height: 30px;
}
/* tz list */
.filter-tz {
    margin-right: 50px;
    float: right;
    height: 80vh;
    overflow: scroll;
}
.filter-tz ul {
    margin: 0;
}
.filter-tz ul li{
    list-style-type: none;
    font-size: 18px;
    border: #ccc 1px solid;
    padding: 10px;
    background-color: #fff;
    width: 486px;
}
.tz-name:hover {
    border: solid 2px #ffa500;
    background-color: rgb(243, 243, 243);
}

.tz-time {
    border: 5px #800000 dotted;
    border-radius: 10px;
    padding: 10px;
    width: 150px;
    height: 180px;
    transition: transform .2s;
    float: left;
    margin-left: 50px;
    margin-top: 50px;
}
.tz-time:hover {
    transform: scale(1.5);
}
.tz-month-year {
    font-size: 14px;
    color: #ccc;
}
.tz-weekday {
    font-family: 'Monoton', cursive;
    font-size: 38px;
    color: #ffa500;
}
.tz-seconds {
    font-size: 18px;
}
.tz-country {
    font-family: 'Monoton', cursive;
    font-size: 18px;
    color: #ffa500;
}
.tz-major-city {
    font-size: 24px
}
